.scherohome {
    padding-top: $pd-top ;
    &__title {
        grid-column: 3/11;
        text-align: center;
        .logo {
            display: inline-block;
            margin: 4px 0 46px;
        }
        &-inner {
            .sub-title {
                @include text(1.6rem,cm,1.9rem,$white-cl,uppercase);
                letter-spacing: .6em;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-bottom: 32px;
                span {
                    @include size(60px,1px);
                    background-color: #DBC283;
                    opacity: .3;
                    margin: 0 37px;
                    display: inline-block;
                }
            }
            .heading {
                font-family: c;
                text-transform: uppercase;
                color: #DBC283;
                margin-bottom: 66px;
            }
        }
    }
    &__video {
        grid-column: 2/12;
        height: 637px;
        margin-bottom: 120px;
    }
}

.scabouthome {
    &__sub-text {
        grid-column: 4/10;
        @include text(2.4rem,il,4rem,$white60-cl,none);
        text-align: center;
        margin-bottom: 110px;
        max-width: 660px;
    }
    &__content {
        grid-column: 1/13;
        @include col(12);
        column-gap: 32px;
        &-inner {
            grid-column: 2/6;
            span {
                @include size(calc(50%+11px), auto);
                display: block;
                border: solid 1px $gold40-cl;
                position: relative;
                .text {
                    margin: 43px 0 41px 90px;
                    @include size(calc(200%-64px),auto);
                    padding: 22px 0 27px;
                    background-color: $background-cl;
                    .--h5 {
                        font-family: cm;
                        margin-bottom: 14px;
                        text-transform: uppercase;
                    }
                    .--h2 {
                        font-family: c;
                        margin-bottom: 22px;
                        text-transform: uppercase;
                        color: $gold-cl;
                    }
                    .--text {
                        color: $white60-cl;
                        margin-bottom: 15px;
                        &.--last {
                            margin-bottom: 32px;
                        }
                    }
                }
            }            
        }
        &-items {
            grid-column: 6/12;
            @include col(3);
            column-gap: 32px;
            row-gap: 32px;
            margin-bottom: 220px;
            .about_item {
                grid-column: 3/4;
                grid-row: 1/2;
                width: 100%;
                padding-bottom: 100%;
                position: relative;
                &-image {
                    position: absolute;
                    top: -1px;
                    left: -1px;
                    bottom: -1px;
                    right: -1px;
                    overflow: hidden;
                    img {
                        @include midimg;
                    }
                    .overlay {
                        position: absolute;
                        top: 0;
                        left: 0;
                        bottom: 0;
                        right: 0;
                        background-color: #000;
                        opacity: .8;
                        overflow: hidden;
                        @include transition($t);
                    }
                }
                .item-line {
                    position: absolute;
                    top: 10px;
                    left: 10px;
                    bottom: 10px;
                    right: 10px;
                    border: solid 1px $gold20-cl;
                    @include transition($t);
                }
                p {
                    position: absolute;
                    left: 27px;
                    bottom: 27px;
                    @include text(2rem,cs,auto,$white-cl,uppercase)
                }
                &.--second {
                    grid-column: 2/3;
                    grid-row: 2/3;
                }
                &.--third {
                    grid-column: 1/2;
                    grid-row: 3/4;
                }
                &.--fourth {
                    grid-row: 3/4;
                }
                &:hover {
                    .overlay {
                        background-color: #332C1D;
                        opacity: .75;
                        @include transition($t);
                    }
                    .item-line {
                        border: solid 1px $gold50-cl;
                        top: -10px;
                        left: -10px;
                        bottom: -10px;
                        right: -10px;
                        @include transition($t);
                    }
                }
                
            }
        }
    }
}

.scproducthome {
    &__logo {
        grid-column: 1/13;
        margin: 0 auto 42px;
    }
    &__title {
        grid-column: 4/10;
        text-align: center;
        .--h2 {
            color: $gold-cl;
            text-transform: uppercase;
        }
    }
    &__product {
        grid-column: 1/13;
        width: 100%;
        height: 664px;        
        margin: 111px auto 250px;
        position: relative;
        .product-features {
            @include fullscreen(false);
            display: flex;
            .features-box {
                text-align: right;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                padding: 26px 188px 26px 0;
                width: 50%;

                .feature-item {
                    display: flex;
                    align-items: center;
                    flex-direction: row-reverse;
                    &:nth-child(2) {
                        margin-right: 92px;
                    }
                    .item-icon {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        @include size(120px);
                        border-radius: 50%;
                        border: solid 1px $white10-cl;
                    }
                    .item-text {
                        margin-right: 24px;
                        max-width: 50%;
                        .--h5 {
                            color: $gold-cl;
                            text-transform: uppercase;
                            margin-bottom: 12px;
                        }
                    }
                }
                &.--right {
                    text-align: left;
                    padding: 26px 0 26px 188px;
                    .feature-item {
                        flex-direction: row;
                        .item-text {
                            margin-right: 0;
                            margin-left: 24px;
                        }
                        &:nth-child(2) {
                            margin-right: 0;
                            margin-left: 92px;
                        }
                    }
                }
            }
        }
        .product-background {
            position: relative;
            @include size(100%);
            .bg-circle {
                @include mid;
                .anim-rotate {
                    @include animation(rotating 36s linear infinite);
                }
                @keyframes rotating {
                    from {
                        @include transform(rotate(0deg));
                    }
                    to {
                        @include transform(rotate(360deg));
                    }
                }
            }
            .bg-blur {
                @include mid;
                width: 28%;
                padding-bottom: 28%;
                border-radius: 50%;
                filter: blur(300px);
                background-color: $white10-cl;
            }
        }
    }
}

.scintrohome {
    background-color: rgba(0,0,0,.3);
    padding: 163px 0 122px;
    &__img {
        grid-column: 1/7;
        height: 100%;
        img {
            height: 685px;
            width: auto;
            margin: 115px 0 -220px 90px;
        }
    }
    &__content {
        grid-column: 8/13;
        .content-title {
            .--h5 {
                margin-bottom: 14px;
                text-transform: uppercase;
            }
            .--h4 {
                color: $gold-cl;
                text-transform: uppercase;
                max-width: 70%;
                margin-bottom: 32px;
            }
        }
        .content-bodytext {
            @include col(2);
            column-gap: 32px;
            margin-bottom: 85px;
            .--text {
                color: $white60-cl;
            }
        }
    }
}

.scfeaturehome {
    padding-top: 315px;
    &__title {
        grid-column: 2/12;
        text-align: center;
        .--h1 {
            color: $gold-cl;
            text-transform: uppercase;
            padding: 0 8%;
            margin-bottom: 90px;
        }
    }
    &__features {
        grid-column: 2/12;
        position: relative;
        .features-box {
            width: 100%;
            display: flex;
            justify-content: space-between;
            .feature-item {
                width: 28%;
                .--h4 {
                    color: $gold-cl;
                    text-transform: uppercase;
                    margin-bottom: 32px;
                }
                .--text {
                    color: $white60-cl;
                }
                &:nth-child(2) {
                    margin-top: 162px;
                    .--h4 {
                        width: 80%;
                        min-width: 300px;
                    }
                }
                &:nth-child(3) {
                    margin-top: calc(162px * 2);
                }
            }
        }
        .features-cta {
            position: absolute;
            right: 189px;
            cursor: pointer;
            .outer-circle {
                @include size(173px);
                border: solid 1px $gold50-cl;
                border-radius: 50%;
                display: inline-block;
                position: relative;
                .cta-signup-text {
                    @include size(150px);
                    @include mid;
                    .anim-rotate {
                        @include animation(rotating 12s linear infinite);
                    }
                    @keyframes rotating {
                        from {
                            @include transform(rotate(0deg));
                        }
                        to {
                            @include transform(rotate(360deg));
                        }
                    }
                }
                .inner-circle {
                    @include size(102px);
                    border: solid 1px $gold50-cl;
                    border-radius: 50%;
                    @include mid;
                    @include mid(true);
                    .ic-arrows {
                        position: relative;
                        @include size(32px);
                        overflow: hidden;
                        .ic-arrow {
                            transform: translate3d(0%, 0%, 0);
                            @include transition(0s);
                            &.clone {
                                position: absolute;
                                top: 0;
                                left: 0;
                                transform: translate3d(-150%, 150%, 0);
                            }
                        }
                       
                    }
                }
                &:hover {
                    .ic-arrows {
                        .ic-arrow {
                            transform: translate3d(150%, -150%, 0);
                            @include transition(transform cubic-bezier(0.4, 0, 0.1, 1) 0.6s);
                            &.clone {
                                transform: translate3d(0%, 0%, 0);
                            }
                        }
                    }
                }
            }
        }
    }
}

.sctestimonialhome {
    padding: 203px 0 176px;
    &__title {
        grid-column: 2/6;
        grid-row: 1/2;
        margin-top: 116px;
        .--h2 {
            text-transform: uppercase;
        }
    }
    &__content {
        grid-column: 6/12;
        grid-row: 1/2;
        border: solid 1px $gold-cl;
        height: 506px;
    }
}

.scbloghome {
    padding: 156px 0;
    background-color: rgba(0,0,0,.5);
    &__title {
        grid-column: 2/12;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 60px;
        .--h3 {
            text-transform: uppercase;
        }
    }
    &__content {
        grid-column: 2/12;
        display: flex;
        justify-content: space-between;
        .blog-item {
            width: 28%;
            display: block;
            .blog-img {
                height: 254px;
                position: relative;
                margin-bottom: 16px;
                overflow: hidden;
                img {
                    @include midimg;
                    opacity: .2;
                    @include transition($t);                    
                }
                .overlay {
                    @include overlay(0);
                    background-color: #93815D;
                    mix-blend-mode: overlay;
                }
                .item-line {
                    @include overlay(10px);
                    border: solid 1px $gold50-cl;
                }
                &:hover {
                    img {
                        opacity: 1;
                        @include transition($t);
                    }
                }
            }
            .blog-title {
                color: $gold-cl;
                margin-bottom: 16px;
            }
            .blog-info {
                display: flex;
                justify-content: space-between;
                padding: 16px 0;
                border-top: solid 1px $gold30-cl;
                border-bottom: solid 1px $gold30-cl;
                .info-block {
                    width: 30%;
                    display: flex;
                    align-items: center;
                    .--text-s {
                        margin-left: 8px;
                        color: $white40-cl;
                    }
                }
            }
        }
    }
}